import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import logo from '../Pages/Assets/properlogo.png';
import ModalVideo from 'react-modal-video'
import './DetailsNew.css'
const DetailsNew = () => {
    const [isOpen, setOpen] = useState(true);
    const [videoId, setVideoId] = useState('')
    const { id } = useParams()
    const fechMoiveVideo = async () => {
        const res = await axios.get(`https://api.themoviedb.org/3/movie/${id}/videos?api_key=ae486947dace2da155505d781142e114&language=en-US`)
        setVideoId(res?.data?.results[5].key)
    }
    useEffect(() => { fechMoiveVideo() }, [])
    return (
        <div>
            <header className='login_header'>
                <img className='login_logo' src={logo} alt='no' />
            </header>
            <div>
                <ModalVideo channel='youtube' autoplay isOpen={isOpen} videoId={videoId} />
            </div>

        </div>
    )
}

export default DetailsNew