import React, { useEffect, useState } from 'react'
import { Col, Row } from 'antd';
import style from './Details.module.css'
import BackArrow from '../Pages/Assets/backarrow.svg';
import VideoPlayer from '../Pages/Assets/player.svg';
import ModalVideo from 'react-modal-video'
import logo from '../Pages/Assets/properlogo.png';
import Img from '../Pages/Assets/scarface.png';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router';
import { Spin } from 'antd';
// import './my.sass.scss';

const Details = () => {
    const [movieDetail, setMovieDetail] = useState([])
    const [isOpen, setOpen] = useState(false);
    const [loading, setLoading] = useState(false);
    const [videoId, setVideoId] = useState('')
    const { id } = useParams()
    const navigate = useNavigate()
    const fechDetails = async () => {
        setLoading(true)
        const res = await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=ae486947dace2da155505d781142e114&language=en-US`)
        setMovieDetail(res.data)
        setLoading(false)
    }
    useEffect(() => {
        fechDetails()
    }, [])
    const fechMoiveVideo = async () => {
        const res = await axios.get(`https://api.themoviedb.org/3/movie/${id}/videos?api_key=ae486947dace2da155505d781142e114&language=en-US`)
        setVideoId(res?.data?.results[5].key)
    }
    useEffect(() => { fechMoiveVideo() }, [])
    return (
        <>
            <header className='login_header'>
                <img className='login_logo' src={logo} alt='no' />
            </header>
            <Row className={style.MovieDetailsRow}>
                <>
                    <Col flex="0 1 650px" className={style.MovieDetails}>
                        < div className={style.MovieDetailsContainer}>
                            {loading ? <Spin tip="Loading" size="large">
                                <div className="content" />
                            </Spin> : ''}
                            <img src={BackArrow} alt="BackArrow" style={{ cursor: "pointer" }} onClick={() => navigate('/home')} id={style.backarrow} />
                            <h1>
                                {movieDetail?.original_title}
                            </h1>
                            <p className={style.rating}>Rating: {(movieDetail?.vote_average / 2).toFixed(1) || 4}/ 5</p>
                            <p className={style.description}>
                                {movieDetail?.overview}
                            </p>
                            <pre>Release Date    {movieDetail?.release_date}</pre>
                            <p className={style.language}>Orginal Language {movieDetail?.spoken_languages?.map((items, i) => {
                                return <span>  {items?.english_name}   </span>
                            })} </p>
                        </div>
                    </Col>
                    <Col flex="1 1 200px" className={style.movieImg}>
                        <div className={style.background} style={{ backgroundImage: `url(https://image.tmdb.org/t/p/w780${movieDetail?.poster_path})`, height: '100vh' }}>
                            <ModalVideo channel='youtube' autoplay isOpen={isOpen} videoId={videoId} onClose={() => setOpen(false)} />
                            <div id={style.id}></div>
                            <div className="videoPlayer">
                                <img id={style.img} src={VideoPlayer} alt="VideoPlayer"
                                    onClick={() => navigate(`/detailsNew/${id}`)}
                                />
                            </div>
                        </div>
                    </Col>
                </>
            </Row>
        </>
    )
}

export default Details